/* MAIN LANDING HEADER 1*/

#wrapper {
    padding-left: 0;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

#wrapper.toggled {
    
}

#sidebar-wrapper {
    z-index: 1000;
    position: fixed;
    right: 380px;
    max-width: 380px;
    height: 100%;
    margin-right: -380px;
    overflow-y: auto;
    background: #EAE6DE;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

#sidebar-wrapper .sidebar-header {
  background-color: #009587;
}

#sidebar-wrapper .sidebar-header a {
  color: white;

}
#sidebar-wrapper .sidebar-subheader {
  background-color: #5F5F5F;
  color: white;

  font-size: 22pt;
  font-family: "Baskerville BT WGL4 W05 Roman";
}

.navlinks a {
  font-family: "Kessel_105 W05 Book";
  color: #5F5F5F;
  font-size: 12pt;
  line-height: 2;
  letter-spacing: .25px;
}

.navlinks a.active {
  font-weight: bold;
}

#wrapper.toggled #sidebar-wrapper {
    width: 380px;
}

#page-content-wrapper {
    width: 100%;
/*    position: absolute;
    height: 100vh;
	overflow-x: hidden;
	overflow-y: auto;
	perspective: 1px;
	transform-style: preserve-3d;*/
}

#wrapper.toggled #page-content-wrapper {
    position: absolute;
}

/* Sidebar Styles */

.sidebar-nav {
    position: absolute;
    top: 0;
    width: 380px;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    list-style: none;
}

.sidebar-nav li {
    text-indent: 20px;
    line-height: 40px;
}

.sidebar-nav li a {
    display: block;
    text-decoration: none;
    color: #999999;
}

.sidebar-nav li a:hover {
    text-decoration: none;
    color: #fff;
    background: rgba(255,255,255,0.2);
}

.sidebar-nav li a:active,
.sidebar-nav li a:focus {
    text-decoration: none;
}

.sidebar-nav > .sidebar-brand {
    height: 65px;
    font-size: 18px;
    line-height: 60px;
}

.sidebar-nav > .sidebar-brand a {
    color: #999999;
}

.sidebar-nav > .sidebar-brand a:hover {
    color: #fff;
    background: none;
}

