.image-description-container {
  padding-top: 0px;
  padding-right: 0px;
  padding-bottom: 2rem;
  padding-left: 0px;
}

.image-description-parent {
  width: 100%;
  height: 100%;
}

.image-description-border {
  position: relative;
}

.image-description-border:after {
  content: "";
  position: absolute;
  top: .5rem;
  left: .5rem;
  right: .5rem;
  display: block;
  width: calc(100% - 1rem);
  height: calc(100% - 1rem);
  border: 1px solid #d5d5d5;
}

*, ::after, ::before {
  box-sizing: border-box;
}

